body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.exchanges-list {
    background-color: #000d0d;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    padding-left: 0px;
}

.exchanges-list li {
    color: #00DD00;
    list-style: none;
    display: inline;
    padding-left: 5px;
    padding-right: 5px;
}

.links>* {
    margin: 0px 2px 0px 2px;
}

.links a:hover {
    box-shadow: 0px 3px;
}

.news {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.new {
    display: flex;
    flex-direction: column;
    width: 30%;
    min-width: 300px;
    max-width: 500px;
}

.new img {
    min-width: 250px;
    min-height: 200px;
    max-width: 350px;
    max-height: 300px;
}

.new-date {
    margin: 2x;
    border: 2px solid #FF4F00;
    border-top: 0px;
    border-left: 0px;
    border-right: 0px;
}

.main-new {
    margin: 20px 0px 40px 0px;
    display: flex;
    overflow: auto;
}

.main-new img {
    width: 50%;
    float: left;
}

.main-description {
    float: right;
    margin: 15px;
}

.article-container {
    display: flex;
    margin-top: 5vh;
    justify-content: center;
}

.article {
    max-width: 700px;
    margin-bottom: 10vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.article-title {
    border: 3px solid #FF4F00;
    border-top: 0px;
    border-left: 0px;
    border-right: 0px;
}

.article-autor {
    border: 3px solid #FF4F00;
    border-top: 0px;
    border-left: 0px;
    border-right: 0px;
}

.article img {
    width: 100%;
    height: 100%;
    margin-top: 2vh;
    margin-bottom: 4vh;
}

footer {
    width: 100%;
    background-color: #343a40;
    color: rgba(255, 255, 255, .5);
    text-align: center;
}

footer p {
    margin-bottom: 0px;
}

@media only screen and (max-width: 600px) {
    .main-new {
        flex-direction: column;
    }
    .main-new img {
        width: 100%;
    }
}
